.gallery-section {
    position: relative;
}

.gallery-container {
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    top: 80px;

    @media screen and (min-width: 768px) {
        top: -20px;
    }

    @media screen and (min-width: 1200px) {
        top: 20px;
    }
}

.slider__item {
    display: flex;
    justify-content: center;
    align-items: center;
}

.slider__img {
    // mobile
    max-width: 100%;

    border-radius: 10px;

    margin-left: auto;
    margin-right: auto;

    // tablet
    @media screen and (min-width: 768px) {
        max-width: 700px;
        
        border-radius: 18px;
    }

    // desktop
    @media screen and (min-width: 1200px) {
        max-width: 1020px;

        border-radius: 24px;
        // border-radius: 24px;
    }
}

.slick-next {
    opacity: 0;
    cursor: none;
}

.slick-arrow {
    opacity: 0;
    cursor: none;
}